body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  padding: 20px;
  text-align: center;
  min-height: 100vh;
  background-color: #282c34;
  color: white;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.complexity {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 20px;
}

.complexity-item {
  margin-right: 50px;
  font-size: 25px;
  font-weight: 600;
  color: gray;
}

.sample-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.sample-case {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.case-label {
  text-align: left;
  font-size: 20px;
  color: gray;
}

.sample-button-list {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin: 10px 50px;
}

.sample-button {
  margin-right: 20px;
  border: 1px solid #9e9e9e;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 20px;
  color: #9e9e9e;
}

.sample-button.active {
  border: 1px solid white;
  color: white;
}

.sample-button:hover {
  border: 1px solid white;
  color: white;
  cursor: pointer;
}

.input-wrapper {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 20px;
}

.input-label {
  margin-right: 10px;
  font-size: 20px;
  color: gray;
}

.input-pan {
  outline: none;
  border: 1px solid gray;
  border-radius: 10px;
  padding: 5px;
  font-size: 20px;
}

.input-rule {
  margin-left: 10px;
  font-size: 20px;
  font-weight: 600;
  color: gray;
}

.input-error {
  position: absolute;
  left: 80px;
  top: 35px;
  font-size: 18px;
  font-weight: 700;
  color: red;
}

.result-layer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-top: 30px;
}

.result-button {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 20px;
  border-radius: 5px;
  padding: 10px;
  font-size: 20px;
  font-weight: 700;
  color: #282c34;
  background: aqua;
  cursor: pointer;
}

.result-button:hover {
  opacity: 0.8;
}

.result-pair {
  font-size: 25px;
  word-spacing: 20px;
}

.result-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 20px;
  max-height: 50vh;
  overflow-y: auto;
}

.result-item {
  word-spacing: 20px;
}

